import { VirtualMailbox } from '@api/models/virtual-mailbox.model';
import { MailboxService } from '../../../core/services/mailbox/mailbox.service';
import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { zip, filter, map, switchMap } from 'rxjs/operators';
import { b64toBlob } from '@core/utils/file.utils';

@Component({
  selector: 'app-mailbox-items',
  templateUrl: './mailbox-items.component.html',
  styleUrls: ['./mailbox-items.component.scss'],
})
export class MailboxItemsComponent implements OnInit, OnDestroy {
  @Input() type: string;
  @Input() item: VirtualMailbox;
  private subs = new Subscription();

  constructor(private mailboxService: MailboxService) {}

  ngOnInit(): void {}

  download(virtualMailbox: VirtualMailbox) {
    this.subs.add(
      this.mailboxService
        .getCorrespondence(virtualMailbox)
        .pipe(
          filter((rs) => !!rs),
          b64toBlob()
        )
        .subscribe((blob) => {
          //Download
          const a = document.createElement('a');
          a.href = URL.createObjectURL(blob);
          a.download = this.item.nomDoc;
          a.click();

          //Open in new window
          // const blobUrl = URL.createObjectURL(blob);
          // window.open(blobUrl);
        })
    );
  }

  ngOnDestroy(): void {
    this.subs.unsubscribe();
  }
}
