import { Customer } from '@core/models/customer.model';
import { MailboxService } from '@core/services/mailbox/mailbox.service';

import { Component, OnInit } from '@angular/core';
import { AccountService, createCustomerMap } from '@core/services/account/account.service';
import { HeaderService } from '@core/services/header/header.service';
import { filter, map } from 'rxjs/operators';
import { zip } from 'rxjs';
import { formatCustomDate } from '@core/utils';
import { FormGroup, FormBuilder, Validators, AbstractControl } from '@angular/forms';
import * as moment from 'moment';
import { DatepickerDateCustomClasses, BsLocaleService } from 'ngx-bootstrap/datepicker';
import { VirtualMailbox } from '@api/models';

@Component({
  selector: 'app-virtual-mailbox',
  templateUrl: './virtual-mailbox.component.html',
})
export class VirtualMailboxComponent implements OnInit {
  state$ = this.createState();
  searchForm: FormGroup;
  maxDateTo = new Date();
  minDateFrom = new Date(moment().subtract(60, 'days').format('L'));
  dateCustomClasses: DatepickerDateCustomClasses[];
  dateFrom: Date;
  dateTo: Date;
  formSubmit: boolean;
  notifications: VirtualMailbox[];
  documents: VirtualMailbox[];
  since: string;
  until: string;
  showResult = false;

  constructor(
    private formBuilder: FormBuilder,
    private bsLocaleService: BsLocaleService,
    private headerService: HeaderService,
    private accountService: AccountService,
    private mailboxService: MailboxService
  ) {}

  ngOnInit(): void {
    this.headerService.updateTitle('menu_mailbox');
    this.bsLocaleService.use('es');
    this.createForm();
    this.searchNotification();
  }

  getUsername(customer: Customer) {
    return `${customer?.name} ${customer?.firstSurname} ${customer?.secondarySurname}`;
  }

  get maxDateFrom() {
    return this.f.dateTo.value ? new Date(this.f.dateTo.value) : new Date();
  }

  get minDateTo() {
    return this.f.dateFrom.value
      ? new Date(this.f.dateFrom.value)
      : new Date(moment().subtract(60, 'days').format('L'));
  }

  private createState() {
    return zip(
      this.accountService.currentCustomer$.pipe(
        filter((customer) => !!customer),
        createCustomerMap()
      )
    ).pipe(
      map(([customer]) => ({
        customer,
      }))
    );
  }

  private createForm() {
    const since = new Date(moment().subtract(60, 'days').format('L'));
    const until = new Date();
    this.searchForm = this.formBuilder.group({
      dateFrom: [since, [Validators.required]],
      dateTo: [until, [Validators.required]],
    });
  }

  searchNotification() {
    this.showResult = false;
    this.notifications = this.documents = [];
    this.since = formatCustomDate(this.f.dateFrom.value);
    this.until = formatCustomDate(this.f.dateTo.value);
    this.mailboxService.checkCorrespondence(this.since, this.until).subscribe((result) => {
      this.showResult = true;
      this.notifications = this.documents = result;
    });
  }

  get f() {
    return this.searchForm.controls;
  }

  hasError(control: AbstractControl) {
    return this.formSubmit && control.invalid;
  }
}
