import { ErrorHandlerService } from '../errors/error-handler.service';
import { VirtualMailbox } from '../../../api/models/virtual-mailbox.model';
import { CasePayload } from '../../../api/models/case-payload.model';
import { Injectable } from '@angular/core';
import { SalesforceResponse, Case, CaseRequest, ApiError } from '../../../api/models';
import { pluck, map, catchError } from 'rxjs/operators';
import { environment } from '@environment/environment';
import { HttpClient } from '@angular/common/http';
import { of, Observable, ObservedValueOf } from 'rxjs';
import { ApiErrors } from '@api/constants';

@Injectable({
  providedIn: 'root',
})
export class MailboxService {
  constructor(private httpClient: HttpClient, private errorHandlerService: ErrorHandlerService) {}

  checkCorrespondence(
    since: string,
    until: string
  ): Observable<Array<VirtualMailbox>> | Observable<any> {
    const url = `${environment.apiBasePath}${environment.endpoints.virtualMailbox.checkCorrespondence}?since=${since}&until=${until}`;
    return this.httpClient.get<Array<VirtualMailbox>>(url).pipe(
      pluck('data'),
      catchError((error: ApiError) => {
        if (error.errorCode) {
          return of([]);
        }
        return this.errorHandlerService.redirectErrorPage(error);
      })
    );
    // return of([
    //   {
    //     visualizado: '0',
    //     visible: '1',
    //     vip: '0',
    //     nomDoc: 'NOTIFICACION LEVANTAMIENTO DE EMBARGO',
    //     nDocum: '000000002',
    //     importe: null,
    //     idioma: '01',
    //     idExterno: '97730095J',
    //     idDocumento:
    //       'ORACLE-20/05/2020-1508-2020-05-21-12-47-03-53-000000002-97730095J -TERCIOSPDF',
    //     fecha: '2020-05-20',
    //     descripcionCategoria: 'AVISOS',
    //     cuenta: '15080001240000070920',
    //     codigoFormulario: 'EMB0CCC1A01',
    //     categoria: '00001',
    //   },
    // ]);
  }

  getCorrespondence(vmb: VirtualMailbox) {
    const url = `${environment.apiBasePath}${environment.endpoints.virtualMailbox.getCorrespondence}?visualized=${vmb.visualizado}&formcode=${vmb.codigoFormulario}&date=${vmb.fecha}&documentid=${vmb.idDocumento}&account=${vmb.cuenta}&externalid=${vmb.idExterno}`;
    return this.httpClient.get<SalesforceResponse<any>>(url).pipe(map((res) => res?.data?.[0]));
  }
}
// operators
export function filterNotification(virtualMailbox: Array<VirtualMailbox>) {
  return virtualMailbox.filter((vmb) => vmb.descripcionCategoria === 'AVISOS');
}

export function filterDocument(virtualMailbox: Array<VirtualMailbox>) {
  return virtualMailbox.filter((vmb) => vmb.descripcionCategoria !== 'AVISOS');
}
